import { BrowserRouter as Router, Switch, Route,Link } from "react-router-dom";
import Home from "./rout/Home";
import Detail from './rout/Detail';

function App() {
    return (
        <Router>
            <Switch>
                <Route path="/movie/:id">
                    <Detail />
                </Route>
                <Route path='/'>
                    <Home />
                </Route>
            </Switch>
        </Router>
    );
}

export default App;
